___CSS_LOADER_EXPORT___.push([module.id, `* {
  font-family: Arial, Helvetica, sans-serif;
  color: gray;
}

.line_container {
  position: relative;
  display: flex;
  flex-direction: column;
  height: 95vh;
}

.line_list {
  display: flex;
  flex-direction: column;
  gap: 10px;
  max-height: 90%;
  overflow: scroll;
}

.line_item {
  display: flex;
  gap: 5px;
  padding: 10px;
  border: 1px solid gray;
  border-radius: 10px;
}

.line_time {
  margin-left: auto;
  margin-right: 0;
  min-width: fit-content;
}

.line_form {
  position: relative;
  display: flex;
  margin-top: auto;
  margin-bottom: 0;
  
}

.form_text {
  width: 100%;
  height: 40px;
  border: 1px solid gray;
  border-radius: 10px;
}

.btn_container {
  position: absolute;
  display: flex;
  align-items: center;
  gap: 10px;
  top: 7px;
  right: 10px;
}

.btn {
  width: 30px;
  height: 30px;
  background-color: white;
  border: 1px solid gray;
  border-radius: 50%;
  cursor: pointer;
}

.custom_geolocation {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  flex-direction: column;
  gap: 10px;

  max-width: 400px;
  background-color: white;
}

.custom_geolocation_text {
  margin: 0;
  padding: 0;
}

.custom_geolocation_content {
  color: inherit;
  font-style: inherit;
}

.custom_geolocation_content::placeholder {
  color: lightgray;
}

.custom_geolocation_btn {
  margin-left: auto;
  margin-right: 0;
}

.geolocation_btn {
  color: inherit;
  padding: 5px;
  border: 1px solid gray;
  background-color: white;
  border-radius: 5px;
}

.media_modal {
  position: absolute;
  z-index: 1;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;

  max-width: 400px;
  background-color: white;
  
  border: 1px solid gray;
  border-radius: 10px;
}

.media_modal_text {
  margin: 0;
  padding: 0;
}

.media_modal_btn {
  margin-left: auto;
  margin-right: 0;
}

.submit_btn {
  color: inherit;
  padding: 5px;
  border: 1px solid gray;
  background-color: white;
  border-radius: 5px;
}

.preview_container {
  position: absolute; 
  display: flex;
  width: 100%;
  padding: 5px;
  bottom: 37px; 
  box-sizing: border-box;
  border: 1px solid gray;
  background-color: white;
  border-radius: 5px 5px 0 0;
}

.preview_video {
  max-height: 200px;
  margin-left: auto;
  margin-right: auto;
}`, "",{"version":3,"sources":["webpack://./src/css/style.css"],"names":[],"mappings":"AAAA;EACE,yCAAyC;EACzC,WAAW;AACb;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,sBAAsB;EACtB,YAAY;AACd;;AAEA;EACE,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,eAAe;EACf,gBAAgB;AAClB;;AAEA;EACE,aAAa;EACb,QAAQ;EACR,aAAa;EACb,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,iBAAiB;EACjB,eAAe;EACf,sBAAsB;AACxB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,gBAAgB;EAChB,gBAAgB;;AAElB;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,mBAAmB;EACnB,SAAS;EACT,QAAQ;EACR,WAAW;AACb;;AAEA;EACE,WAAW;EACX,YAAY;EACZ,uBAAuB;EACvB,sBAAsB;EACtB,kBAAkB;EAClB,eAAe;AACjB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,sBAAsB;EACtB,SAAS;;EAET,gBAAgB;EAChB,uBAAuB;AACzB;;AAEA;EACE,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,cAAc;EACd,mBAAmB;AACrB;;AAEA;EACE,gBAAgB;AAClB;;AAEA;EACE,iBAAiB;EACjB,eAAe;AACjB;;AAEA;EACE,cAAc;EACd,YAAY;EACZ,sBAAsB;EACtB,uBAAuB;EACvB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,UAAU;EACV,QAAQ;EACR,SAAS;EACT,gCAAgC;EAChC,aAAa;EACb,sBAAsB;EACtB,SAAS;EACT,aAAa;;EAEb,gBAAgB;EAChB,uBAAuB;;EAEvB,sBAAsB;EACtB,mBAAmB;AACrB;;AAEA;EACE,SAAS;EACT,UAAU;AACZ;;AAEA;EACE,iBAAiB;EACjB,eAAe;AACjB;;AAEA;EACE,cAAc;EACd,YAAY;EACZ,sBAAsB;EACtB,uBAAuB;EACvB,kBAAkB;AACpB;;AAEA;EACE,kBAAkB;EAClB,aAAa;EACb,WAAW;EACX,YAAY;EACZ,YAAY;EACZ,sBAAsB;EACtB,sBAAsB;EACtB,uBAAuB;EACvB,0BAA0B;AAC5B;;AAEA;EACE,iBAAiB;EACjB,iBAAiB;EACjB,kBAAkB;AACpB","sourcesContent":["* {\n  font-family: Arial, Helvetica, sans-serif;\n  color: gray;\n}\n\n.line_container {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  height: 95vh;\n}\n\n.line_list {\n  display: flex;\n  flex-direction: column;\n  gap: 10px;\n  max-height: 90%;\n  overflow: scroll;\n}\n\n.line_item {\n  display: flex;\n  gap: 5px;\n  padding: 10px;\n  border: 1px solid gray;\n  border-radius: 10px;\n}\n\n.line_time {\n  margin-left: auto;\n  margin-right: 0;\n  min-width: fit-content;\n}\n\n.line_form {\n  position: relative;\n  display: flex;\n  margi